import * as React from 'react';
import { Row, Col, Divider, Input, Drawer, Button, Form, Radio, Modal, Progress } from 'antd';
import { Redirect } from 'react-router-dom';
import { SketchPicker } from 'react-color'
import axios from 'axios';
import './index.scss';
const RadioGroup = Radio.Group;

interface IState {
    visible: boolean,
    currentColor: string,
    currentInput: string,
    sendTarget: number,
    template: ITemplate,
    tagsArr: string[],
    progress: number,
    paramsArr: string[],
    access_token: string
}
interface ITemplate {
    template_id: string;
    title: string;
    primary_industry: string;
    deputy_industry: string;
    content: string;
    example: string;
}
class Send extends React.Component<any, IState>{
    constructor(props: any) {
        super(props);
        const wechatAccount = JSON.parse(localStorage.getItem('wechat_account') as string);
        if (wechatAccount && new Date().getTime() < wechatAccount.expires_time) {
            this.state = {
                visible: false,
                currentColor: '',
                currentInput: '',
                sendTarget: 1,
                template: this.props.location.state.query,
                tagsArr: [],
                paramsArr: [],
                progress: 0,
                access_token: wechatAccount.access_token,
            }
        }
    };

    public componentWillMount = () => {
        const { template } = this.state;
        this.getDataItem(template.content);
        this.getTagList()
    };

    public render() {
        if (!this.state) {
            return <Redirect to='/auth_manage' />
        } else {
            const { template, paramsArr, currentColor, tagsArr, progress } = this.state;
            const { getFieldDecorator } = this.props.form;
            return (
                <div>
                    <Row type='flex' justify='center' className='row-item'>
                        <Col span={6}>ID</Col>
                        <Col span={18}>{template.template_id}</Col>
                    </Row>
                    <Divider dashed />
                    <Row type='flex' justify='center' className='row-item'>
                        <Col span={6}></Col>
                        <Col span={18}>{template.title}</Col>
                    </Row>
                    <Divider dashed />
                    <Row type='flex' justify='center' className='row-item'>
                        <Col span={6}></Col>
                        <Col span={18}>{template.primary_industry}-{template.deputy_industry}</Col>
                    </Row>
                    <Divider dashed />
                    <Row type='flex' justify='center' className='row-item'>
                        <Col span={6}></Col>
                        <Col span={18}>
                            <pre>
                                {(template.content)}
                            </pre>
                        </Col>
                    </Row>
                    <Divider />
                    <Drawer
                        title=""
                        placement="left"
                        closable={true}
                        onClose={this.onColorSelectClose}
                        visible={this.state.visible}
                    >
                        <SketchPicker
                            onChangeComplete={this.selectColor}
                            color={currentColor}
                        />
                        <div style={{ textAlign: "center", marginTop: 100 }}>
                            <span style={{ color: currentColor }}>{currentColor}</span>
                            <Divider />
                            <Button type="primary" onClick={this.setColor}>
                                
                        </Button>
                        </div>
                    </Drawer>
                    <Form onSubmit={this.sendMessageSubmit} className="login-form">
                        {paramsArr ? paramsArr.map((val: string, i: number) => (
                            <Row type='flex' justify='center' className='row-item' key={i}>
                                <Col span={3} style={{ textAlign: "right", lineHeight: 2.5 }}>
                                    <span className='redPoint'>*</span>
                                    <span style={{ marginRight: 35 }}>{val}</span>
                                </Col>
                                <Col span={14}>
                                    <Form.Item>
                                        {getFieldDecorator(`${val}`, {
                                            rules: [{ required: true, message: `${val}!` }],
                                        })(
                                            <Input type='text' placeholder='' />
                                        )}
                                    </Form.Item>
                                </Col>
                                <Col span={2} style={{ textAlign: "right", lineHeight: 2.5 }}>
                                    <b style={{ marginRight: 10 }}>Color</b>
                                </Col>
                                <Col span={5}>
                                    <Form.Item>
                                        {getFieldDecorator(`${val}Color`, {
                                            rules: [{ required: false }],
                                        })(
                                            <Input type='text' style={{ color: this.props.form.getFieldValue(`${val}Color`) }} placeholder=":#FF0000," onClick={() => this.showDrawer(`${val}Color`)} />
                                        )}
                                    </Form.Item>
                                </Col>
                            </Row>
                        )) : <span className='error'>!</span>}
                        <Row type='flex' justify='center' className='row-item'>
                            <Col span={3} style={{ textAlign: "left", lineHeight: 2.5 }}>
                                <span style={{ marginRight: 35 }}>URL</span>
                            </Col>
                            <Col span={14}>
                                <Form.Item>
                                    {getFieldDecorator('redirect_url', {
                                        rules: [{ required: false }],
                                    })(
                                        <Input type='text' placeholder='(http,)' />
                                    )}
                                </Form.Item>
                            </Col>
                            <Col span={7} />
                        </Row>
                        <Row type='flex' justify='center' className='row-item'>
                            <Col span={3} style={{ textAlign: "left", lineHeight: 2.5 }}>
                                <span className='redPoint'>*</span>
                                <span style={{ marginRight: 35 }}></span>
                            </Col>
                            <Col span={21} style={{ lineHeight: 2.5 }}>
                                {getFieldDecorator('send_target', {
                                    rules: [{ required: true, message: '' }],
                                })(
                                    <RadioGroup onChange={this.selectSendTarget}>
                                        {tagsArr ? tagsArr.map((val: any, i: number) => (
                                            val.count === 0
                                                ?
                                                <Radio value={val.id} key={i} disabled>{val.name}({val.count})</Radio>
                                                :
                                                <Radio value={val.id} key={i}>{val.name}({val.count})</Radio>
                                        )) : <span className='error'>!</span>}
                                    </RadioGroup>
                                )}
                            </Col>
                        </Row>
                        <div style={{ textAlign: 'center' }}>
                            <Progress percent={progress} size="small" />
                            <Button type="primary" htmlType="submit">
                                
                        </Button>
                        </div>
                    </Form>
                </div>
            )
        }
    };

    // 
    private getDataItem = (data: string) => {
        if (!data) {
            return
        }
        const regexp = /{{(.*?)}}/g;
        const arr = data.match(regexp);
        const paramsArr = [];
        for (const value of arr as string[]) {
            const val = value.replace(/{{/g, "").replace(/.DATA}}/g, "");
            paramsArr.push(val)
        }
        this.setState({
            paramsArr
        })
    };

    // 
    private showDrawer = (val: string) => {
        this.setState({
            visible: true,
            currentInput: val,
        })
    };

    private setColor = () => {
        const json = {};
        const inputName = this.state.currentInput;
        const currentColor = this.state.currentColor;
        json[inputName] = currentColor;
        this.props.form.setFieldsValue(json);
        this.onColorSelectClose()
    };

    private onColorSelectClose = () => {
        this.setState({
            visible: false,
        })
    };

    private selectColor = (color: any) => {
        this.setState({
            currentColor: color.hex
        })
    };

    // 
    private getTagList = () => {
        axios.get(`/api/wechat/cgi-bin/tags/get`, {
            params: {
                access_token: this.state.access_token
            }
        }).then(res => {
            if (res.data.errcode) {
                Modal.error({
                    title: '',
                    content: res.data.errmsg
                })
            }
            const tagsArr = res.data.tags;
            this.setState({
                tagsArr
            })
        }).catch(err => {
            Modal.error({
                title: '',
                content: ''
            })
        })
    };

    private selectSendTarget = (e: any) => {
        this.setState({
            sendTarget: e.target.value,
        })
    };

    // post
    private sendMessageSubmit = (e: any) => {
        e.preventDefault();
        this.props.form.validateFields((err: any, values: any) => {
            if (!err) {
                const data = {};
                const target = 'send_target';
                for (const param of this.state.paramsArr) {
                    data[param] = {};
                    data[param].value = values[param];
                    if (values[`${param}Color`]) {
                        data[param].color = values[`${param}Color`]
                    }
                }
                this.getTagUsers(data, values[target], values.redirect_url)
            } else {
                if (err.send_target) {
                    Modal.error({
                        title: '',
                        content: err.send_target.errors[0].message
                    })
                }
            }
        })
    };

    // 
    private getTagUsers = (templateData: {}, tagId: string, url?: string) => {
        axios.post(`/api/wechat/cgi-bin/user/tag/get?access_token=${this.state.access_token}`, {
            tagid: tagId
        }).then(res => {
            if (res.data.errcode) {
                Modal.error({
                    title: 'OPEN_ID',
                    content: res.data.errmsg
                })
                return
            }
            if (res.data.count > 0) {
                this.sendMessageRun(templateData, res.data.data.openid, url)
            }
        }).catch(err => {
            Modal.error({
                title: 'OPEN_ID',
                content: ''
            })
        })
    };

    // 
    private sendMessageRun = (templateData: {}, openidArr: [], url?: string) => {
        const openidArrLength = openidArr.length;
        const postData = {
            template_id: this.state.template.template_id,
            data: templateData
        };
        if (url) {
            const urlName = 'url';
            postData[urlName] = url
        }
        for (let i = 0; i < openidArrLength; i++) {
            const touserName = 'touser';
            postData[touserName] = openidArr[i];
            this.sendMessageAjax(postData);
            this.setState({
                progress: ((i + 1) / openidArrLength) * 100
            });
            if (i + 1 === openidArrLength) {
                Modal.success({
                    title: '',
                    content: '',
                })
            }
        }
    };

    // 
    private sendMessageAjax(postData: {}) {
        axios.post(`/api/wechat/cgi-bin/message/template/send?access_token=${this.state.access_token}`, postData).then(res => {
            if (res.data.errcode !== 0) {
                Modal.error({
                    title: '',
                    content: res.data.errmsg
                })
                return
            }
            const touserName = 'touser';
            console.log(`${postData[touserName]}`)
        }).catch(err => {
            Modal.error({
                title: '',
                content: ''
            })
        })
    }
};
const SendTemplate = Form.create()(Send);
export default SendTemplate



